@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@800&family=Poppins&family=Zilla+Slab:ital,wght@1,300;1,400;1,600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@800&family=Poppins&family=Zilla+Slab:ital,wght@1,300;1,400;1,600&display=swap'); 
body {
  margin: 0;
  font-family: "outfit", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
  


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.app{
height: 100vh;
background-image: url("Image/normal.jpg")  ;
background-repeat: no-repeat;
background-position: center center;
background-attachment: fixed;
background-size: cover;
transition: 0.4 ease-out;
   
}
