
*{
    padding:0;
    margin:0;
    box-sizing: border-box;
    color:#fff;
    overflow: none;
}

.main-container{
    position: absolute;
    left: 25%;
    top: 5%;
    width: calc(50% - 50px);
   height: 700px;
   background-color: rgba(7, 7, 7, 0.5);
   border-radius: 20px;
   
   
}
  


input{
position: relative;
left: calc(50% - 150px);
top: 30px;
padding:10px;
width:50%;
border-radius: 20px;
background-color: rgba(255,255,255,0.2);
border: 2px solid rgba(255,255,255);
font-size: 1rem;
font-weight: 300;

}
input::placeholder{
    color:rgba(255,255,255,0.5);
}
input:focus{
    outline:none;
}

 p{
       font-size: 1.6rem;
       
}

.outfit{
display: flex;
justify-content: space-evenly;
background-color: rgba(255,255,255,0.2);
width: 80%;
margin: 6rem auto;
padding:1rem;
border-radius: 12px;
box-shadow: 3px 6px rgba(0,0,0,0.2);
}
.outfit{
    position: relative;
    top:20%;
}
.temp{
    position: relative;
    top: 20%;
    left: 40px;
    font-size: 8rem;
    
}
.Location{
    position: absolute;
    top: 30%;
    left:55%;
    font-size: 2rem;
    font-family: 'Times New Roman', Times, serif;
}
.clouds{
    position: absolute;
    top: 60%;
    left:45%;
    font-family: Georgia, 'Times New Roman', Times, serif;
    
}
 #cel{
    position: relative;
    bottom:55px;
    font-size: 5rem;
    font-weight: 100;
}
span{
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.Location .date{
  color:#fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(99, 96, 96, 0.5);  
}
.errormessage{
   position: relative;
   top:20%;
   text-align: center;
   font-size:'Outfit', sans-serif;
}


@media only screen and (max-width:460px){

.main-container{
    justify-items: center;
    background-color: transparent;
}
input{
    left:calc(50% - 120px);
    width:80vw;
}
.outfit{
    top:30%;
    right:50%;
    width:80vw;

}
.temp{
    top:16px;
    left:calc(50% - 150px);
    font-size: 10rem;
    
}
#cel{
    font-size: 4rem;
}
.Location{
  position: relative;
  top:75%;
  left:calc(50% - 120px);
text-align: center;
 width:80vw;
font-size: 1rem;

}
.clouds{
    left:calc(50% - 20px);
    width:30vw;
    
}
.errormessage{
    left:-20%;
    width:30vh;
}

}
@media only screen and (max-width:360px){
    .main-container{
        justify-items: center;
       
      
    }
    input{
        left:calc(50% - 120px);
        width:80vw;
    }
    .outfit{
        top:30%;
        right:50%;
        width:80vw;
    
    }
    .temp{
        top:16px;
        left:calc(50% - 100px);
        font-size: 8rem;
        
    }
    #cel{
        font-size: 4rem;
    }
    .Location{
      position: relative;
      top:70%;
      left:calc(50% - 120px);
    text-align: center;
     width:80vw;
    font-size: 1rem;
    
    }
    .clouds{
        left:calc(50% - 20px);
        width:40vw;
        
    }
    .errormessage{
        left:-20%;
        width:30vh;
    }
    
    }
    
